<template>
    <div class="tabbar flex-row-start">
        <div 
            class="tabbar-item flex-1 flex-column-center pointer" 
            v-for="item in navList" 
            :key="item.name"
            @click="togglePage(item.path)">
            <img :src="item.icon" class="tabbar-item__icon" />
            <p class="tabbar-item__name" :class="{'active': $route.path == item.path}">{{item.name}}</p>
        </div>
        <el-dropdown trigger="click" class="tabbar-item flex-column-center flex-1">
            <div class="tabbar-item flex-1 flex-column-center pointer">
                <img src="~@/assets/images/common/more.png" class="tabbar-item__icon" />
                <p class="tabbar-item__name">More</p>
            </div>
            <template #dropdown>
                <el-dropdown-menu>
                    <el-dropdown-item 
                        class="flex-row-start nav-list__item select pointer" 
                        v-for="item in moreList" :key="item.name"
                        @click="togglePage(item.path)">
                        <img :src="item.icon" class="nav-list__icon" />
                        <span class="nav-list__text" :class="{'active': $route.path == item.path}">{{item.name}}</span>
                    </el-dropdown-item>
                </el-dropdown-menu>
            </template>
        </el-dropdown>
    </div>
</template>

<script>
export default {
    name: 'TabBar',
    props: {
        navList: Array,
        moreList: Array
    },
    methods: {
        togglePage(path) {
            this.$emit('click', path)
        }
    }
}
</script>

